import { Component, OnInit } from '@angular/core';
import { PostssumService } from '../services/postssum.service';
import { PostSummary } from '../services/data';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  postTitleImage: string;
  posts: PostSummary[];
  constructor(private postsSumService: PostssumService) {
    this.postTitleImage = 'assets/test_post_title.png';
  }

  ngOnInit() {
    this.posts = this.postsSumService.posts;
    this.posts.sort((a, b) => {
      if (a.creationDate > b.creationDate) { return 1; }
      if (a.creationDate < b.creationDate) { return +1; }
      return 0;
    });
  }

}
