export class PostContent {
    content: string;
    type: string;
}

export let postContentMap: Map<string, PostContent[]> = new Map<string, PostContent[]>();


postContentMap.set('try_to_log_effectively',  [
    {type:'h2', content: 'Try to log effectively!'},
    {type:'p',  content: 'Logging plays an important role in software development. Actually the main purpose of logging is to monitor what your software is doing, especially when it’s not possible for you to debug it, e.g. production environment. Whenever I encounter a bug or an error in the product I have developed, the only place which I can elicit some information from it, is the log file, so logging appropriately is an essential part of developing.'}, 
    {type:'p',  content: 'I’m just going to talk about approaches I take and configurations I use in my development environment.'},   
    {type:'h4', content: 'Configurations'},
    {type:'p',  content: 'In my current project, I’m using log4j as the logging framework. There are some reasons that I’ve choose it. Actually it’s a mature framework with lots of document, it’s popular and well-known, and also it’s the one that I’m most knowledgeable about.'},
    {type:'p',  content: '1. Use a meaningful pattern for your logs. You are going to use your log file as a source of runtime information, so you have to get the most out of it, therefore consider these items in your logs:'},
    {type:'p',  content: '   a. Date and Time\r\n   b. Thread name\r\n   c. Full qualified class name\r\n   d. Method name\r\n     e. Log message'},


    {type:'cprprts',  content: 'log4j.appender.rollingAppender=org.apache.log4j.DailyRollingFileAppender\r\nlog4j.appender.rollingAppender.File=path/to/logfile/logfilename.log\r\nlog4j.appender.rollingAppender.layout=org.apache.log4j.PatternLayout\r\nlog4j.appender.rollingAppender..layout.ConversionPattern=%d{dd MMM yyyy HH:mm:ss}-%t-%p-%C-%M-%m%n'},

    {type:'p',   content: 'The above lines of log4j configuration results in a log message like the below one:'},
    {type:'cprprts',  content: '10 Aug 2016 12:49:37-main-INFO-net.ali4j.log4jtest.TestClass-test-this is the log message'},
    {type:'p',   content: 'this line of log message is based on the conversion pattern that is used in configuration lines, we can check each part of coversion pattern separately regarding log message.'},

    {type:'cprprts', content:'%d{dd MMM yyyy HH:mm:ss}:10 Aug 2016 12:49:37\r\n-%p:-INFO-%C:-net.ali4j.log4jtest.TestClass\r\n-%M:-test\r\n-%mn:-this is the log message + \“platform dependent line separator character\”'},

    {type:'p',  content: '2. Try to break your log file into smaller ones. By using DailyRollingFileAppender class, log4j can create a separate log file for every single day, moreover, the current date is appended at the end of the file name which gives you more control over the logs. As you can see DailyRollingFileAppender class is used as the rollingAppender, similarly FileAppender can be used too but as a result we are going to have only one log file.'},

    {type:'p',  content: '3. In addition to the daily file name, you can put a limit on the size of the log file and break your log file based on its size. By adding below line of configuration you can achieve this feature:'},
    {type:'cprprts',  content: '    log4j.rollingAppender.MaxFileSize=10KB/10MB/10GB'},
    {type:'p',  content: '    The only point you have to notice is that just DailyRollingFileAppender and RollingFileAppender classes have this property so you can’t use this configuration for FileAppender class.'},
    
    {type:'p',  content: '4. You can configure log4j to put log files in some specific location. In the above mentioned configurations there is an absolute path to the log file location, suppose that we want to put log files in the log directory of application server, for instanec apache tomcat, inorder to achieve this, you can use ${catalina.home} system property to choose log files location dynamically.'},
    {type:'cprprts',  content: '   log4j.appender.rollingAppender.File=${catalina.home}/logs/yourlogfilename.log'},
    {type:'pl',  content: '   In addition to ${catalina.home} system property you can define your own property when you run your jvm and use it in log4j configuration file.'},

    {type:'h4', content: 'Ways of logging'},
    {type:'p',  content: 'The approach you take to log requires special attention, notice that the information you log is going to be used in production environment as a source of data to monitor your application, so keep these tips in mind:'},
    {type:'polli',  content: ' 1. Use different log levels for logging. There are different log levels in logging frameworks(log4j too) which are used for particular purposes. But these are the most commonly used ones:'},
    {type:'p',  content: '    DEBUG<INFO<ERROR'},
    {type:'p',  content: '    INFO: use this level to log changes in the state of the application, for instance, when new user is registered, some transaction has been commited, a record has been added to the database, or anything that has been done successsfully or unsuccessfully.'},
    {type:'p',  content: '    DEBUG: as it’s name shows this level is used for debugging. You can use debug level to log vast range of data such as input and output of methods, thread states, user inputs, incoming requests, outgoing responses, validation states and so on. Generally anything in detail should be logged by using this level.'},
    {type:'p',  content: '    ERROR: this level is most commonly used to log exceptions and some kind of situations that system can not tolerate such as NPE, database unavailability and … . Logs of this levels show that something hazardous has happened and has to be taken care as soon as possible.'},
    
    {type:'p',  content:  ' 2. Remember to use Logging Guards:'},
    {type:'cj',  content: '    if(_logger.isDebugEnabled()) _logger.debug(“input of method:” + input);'},
    {type:'p',    content: '    Whenever you use debug level to log a message, check if debug level is enabled and then log it. The purpose of this guard is not to prevent log message from being written, actually it’s intended to prevent useless calls to toString() which can be quite expensive.'},
    
    {type:'p',    content: 'That’s all about my experience with logging in java. Whenever you decide to use logging in your application(ofcourse you should use) just keep in mind that later you need this information, so try to log effectively!'},
]);



postContentMap.set('hibernate_entites_custom_id_generator', 
[
    {type: 'h2', content: 'Hibernate Entities Custom Id Generator'},
    {type: 'p', content: 'Generating a custom id for the entities while using hibernate is quite simple. In the scenario I faced I had to use an id generator that starts from a specific id value, I had some records previously inserted into the table with their own id value and for this reason I needed to create a custom id generator for the entities.'},
    {type: 'p', content: 'In the first place you need to add a sequence to the db(in my scenario I had to use a sequence in sql server 2012), in the second step org.hibernate.id.IdentifierGenerator has to be implemeneted and a body for generate method should be provided. and finally newly created id generator can be used as the strategy in @GenericGenerator annotation.'},
    {type: 'p', content: 'This is the first step, sequnce cration actually:'},
    {type: 'csql', content: 'CREATE SEQUENCE [dbo].[Custom_Sequence] AS [BIGINT] START WITH 61000 INCREMENT BY 1 MINVALUE -9223372036854775808 MAXVALUE 9223372036854775808 CACHE'},
    {
        content: 'second step includes implementing IdentifierGenerator:',
        type: 'p'
    }, {
        content: 'public class NewDataIdGenerator implements IdentifierGenerator { private static final Log logger = LogFactory.getLog(NewDataIdGenerator.class); @Override public Serializable generate(SessionImplementor session, Object obj) throws HibernateException { Connection con = session.connection(); Long nextValue = null; try { PreparedStatement p = con.prepareStatement("SELECT NEXT VALUE FOR Custom_Sequence as nextVal"); ResultSet rs = p.executeQuery(); while(rs.next()) { nextValue = rs.getLong("nextVal"); } } catch (SQLException e) { e.printStackTrace(); } if(logger.isDebugEnabled()) logger.debug("new id is generated:" + nextValue); return nextValue; } }',
        type: 'cj'
    }, {
        content: 'and the last step is using custom generator as a strategy in the entity:',
        type: 'p'
    }, {
        content: 'import javax.persistence.Entity;import javax.persistence.GeneratedValue;import javax.persistence.Id;import org.hibernate.annotations.GenericGenerator;@Entitypublic class CustomEntity {@Id @GenericGenerator(name="seq_id", strategy="ir.gov.tax.services.bita.exposed.sendcustomsdeclarationserialnumberservice.idgenerator.NewDataIdGenerator") @GeneratedValue(generator="seq_id")private Long id; /*getter & setter*/}',
        type: 'cj'
    }, {
        content: 'Important point that has to be kept in mind is that with the usage of Hibernate classes (not jpa api) the code gets dependent to the framework and the maintenance of the code will face problems.',
        type: 'p'
    }
]);






